import React,{Component} from 'react';
import PropTypes from 'prop-types';
import {Mutation} from 'react-apollo';
import Router from 'next/router';
import Form from './styles/Form';
import formatMoney from '../lib/formatMoney';
import gql from 'graphql-tag';
import styled from 'styled-components';
import ErrorMessage from './ErrorMessage';


const Center = styled.div`
 text-align:center;
`;

const CREATE_ITEM_MUTATION = gql`
 mutation CREATE_ITEM_MUTATION(
   $description:String!
   $title:String!
   $price:Int!
   $largeImage:String
   $image:String
 ){
   createItem(
     description:$description
     title:$title
     price:$price
     largeImage:$largeImage
     image:$image
   )
 }
`
class CreateItem extends Component{
  state = {
    title:'default',
    description: 'default',
    image: 'no.png',
    largeImage: 'nope.png',
    price:0
  }

  onChange = (e)=>{
    const{name,value,type} = e.target;
    const val = type === "number" ? parseFloat(value) : value;
    this.setState({[name]:val});
  }
  render(){
    return(
     <Mutation mutation={CREATE_ITEM_MUTATION } variables ={this.state}
     >
      {(createItem,{loading,error})=>(
        <Form onSubmit={(e)=>{
          e.preventDefault();
          console.log(this.state);
        }}>
         <fieldset>
         Title
          <label htmlFor="title">
           <input onChange={this.onChange} placeholder="title"
           requiredvalue={this.state.title} id="title" name="title"
           type="text" />
          </label>
          Price
           <label htmlFor="price">
            <input onChange={this.onChange} placeholder="price"
            requiredvalue={this.state.price} id="price" name="price"
            type="number" />
           </label>
           Description
            <label htmlFor="price">
             <textarea onChange={this.onChange} placeholder="description"
             requiredvalue={this.state.description}
             id="description" name="description"
             />
            </label>
            <button type="onSubmit">Submit</button>
         </fieldset>
        </Form>

      )}
     

      </Mutation>
    )
  }
}








export default CreateItem;
export { CREATE_ITEM_MUTATION };
